@font-face {
  font-family: 'Raleway';
  src: local("Raleway Regular"), local("Raleway-Regular"), url("../fonts/raleway/ralewayregular.woff2") format("woff2"), url("../fonts/raleway/ralewayregular.woff") format("woff"), url("../fonts/raleway/ralewayregular.ttf") format("truetype");
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: 'Raleway';
  src: local("Raleway Bold"), local("Raleway-Bold"), url("../fonts/raleway/ralewaybold.woff2") format("woff2"), url("../fonts/raleway/ralewaybold.woff") format("woff"), url("../fonts/raleway/ralewaybold.ttf") format("truetype");
  font-weight: 700;
  font-style: normal;
}

@font-face {
  font-family: 'Raleway';
  src: local("Raleway ExtraBold"), local("Raleway-ExtraBold"), url("../fonts/raleway/ralewayextrabold.woff2") format("woff2"), url("../fonts/raleway/ralewayextrabold.woff") format("woff"), url("../fonts/raleway/ralewayextrabold.ttf") format("truetype");
  font-weight: 800;
  font-style: normal;
}

@font-face {
  font-family: 'Roboto';
  src: local("Roboto Light"), local("Roboto-Light"), url("../fonts/roboto/Robotolight.woff2") format("woff2"), url("../fonts/roboto/Robotolight.woff") format("woff"), url("../fonts/roboto/Robotolight.ttf") format("truetype");
  font-weight: 300;
  font-style: normal;
}

@font-face {
  font-family: 'Roboto';
  src: local("Roboto"), local("Roboto-Regular"), url("../fonts/roboto/Roboto.woff2") format("woff2"), url("../fonts/roboto/Roboto.woff") format("woff"), url("../fonts/roboto/Roboto.ttf") format("truetype");
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: 'icomoon';
  src: url("../fonts/icomoon/icomoon.eot?g4lw8w");
  src: url("../fonts/icomoon/icomoon.eot?g4lw8w#iefix") format("embedded-opentype"), url("../fonts/icomoon/icomoon.ttf?g4lw8w") format("truetype"), url("../fonts/icomoon/icomoon.woff?g4lw8w") format("woff"), url("../fonts/icomoon/icomoon.svg?g4lw8w#icomoon") format("svg");
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

[class^="icon-"], [class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icomoon' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon-vk:before {
  content: "\e900";
}

.icon-puzzle:before {
  content: "\e901";
}

.icon-instagram:before {
  content: "\e902";
}

.icon-gear:before {
  content: "\e903";
}

.icon-arrow:before {
  content: "\e904";
  color: #fff;
}

.icon-additionally:before {
  content: "\e905";
}

::placeholder {
  color: #666;
}

::selection {
  background-color: #e7746f;
  color: #fff;
}

html {
  font-size: 10px;
  font-family: "Roboto", "Arial", sans-serif;
}

body {
  position: relative;
  height: 100%;
  min-width: 320px;
  overflow-x: hidden;
  color: #333;
  font-size: 16px;
}

body input, body textarea {
  border: #666 1px solid;
  outline: none;
}

body input:focus:required:invalid, body textarea:focus:required:invalid {
  border-color: red;
}

body input:required:valid, body textarea:required:valid {
  border-color: green;
}

img {
  width: 100%;
  max-width: 100%;
  height: auto;
}

.img-cover {
  display: flex;
  width: 100%;
  height: 100% !important;
  object-fit: cover;
}

b, strong {
  font-weight: bold;
}

ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

a {
  display: block;
  color: #2e2e2e;
}

a:hover {
  text-decoration: none;
}

a:active, a:focus {
  border: none;
  outline: none;
}

button, figure {
  color: inherit;
  border: none;
  outline: none;
}

button:focus, button:active, figure:focus, figure:active {
  outline: none;
  border: none;
}

select:focus, select:active {
  outline: none;
}

header, section, footer {
  position: relative;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}

.wrapper {
  display: flex;
  flex-direction: column;
  width: 100vw;
  max-width: 100%;
  height: 100%;
  min-height: 100vh;
}

.page-footer {
  margin-top: auto;
}

.button {
  display: inline-block;
  color: #fff;
  background-color: #e7746f;
  padding: 15px 45px;
  font-size: 13px;
  text-transform: uppercase;
  font-weight: 700;
  border-radius: 2px;
  text-align: center;
  position: relative;
  transition: 0.25s ease;
  cursor: pointer;
}

.button:focus, .button:hover {
  text-decoration: none;
  color: #fff;
}

.button:hover {
  background-color: #eb8985;
}

.button:hover::after {
  opacity: .22;
}

.button:active {
  background-color: #e35f59;
}

/* Typography */
.h1-title,
.h2-title,
.h3-title,
.h4-title {
  font-family: "Raleway", sans-serif;
}

.h1-title {
  font-size: 38px;
  font-weight: 800;
}

.h2-title {
  font-size: 30px;
  font-weight: 800;
}

.h3-title {
  font-size: 16px;
  font-weight: 700;
}

.h4-title {
  font-size: 22px;
  font-weight: 300;
}
